import { useState, useEffect } from "react";
import axios from "axios";
import { ChatInterface } from "types/chat";

import css from "./chat.module.scss";

const Chat = () => {
  // States
  const [data, setData] = useState({
    name: "",
    message: "",
  });
  const [chats, setChats] = useState<any[]>([
    {
      name: "Nada Ayu Wardani",
      message: "Selamat kaka",
      date: "2021-08-19T09:26:07.173Z",
      id: 1,
    },
    {
      name: "Aura",
      message: "Selamat kaka",
      date: "2021-08-19T09:27:00.933Z",
      id: 2,
    },
    {
      name: "Jajang",
      message: "Congrats boy",
      date: "2021-08-19T09:36:49.311Z",
      id: 3,
    },
  ]);

  // Handlers
  const handleChange = (event: any) => {
    const { value, name } = event.target;
    setData((state) => ({ ...state, [name]: value }));
  };

  const handleClick = async () => {
    const { name, message } = data;

    setChats((state) => [
      { id: chats.length + 1, name, message, date: new Date() },
      ...state,
    ]);
    setData({ name: "", message: "" });

    // await axios({
    //   url: "http://localhost:5000/comments",
    //   method: "POST",
    //   headers: { "Content-Type": "application/json" },
    //   data: JSON.stringify({ name, message, date: new Date() }),
    // });

    // setTimeout(() => {
    //   getChats();
    // }, 1000);
  };

  // const getChats = async () => {
  //   const res = await axios({
  //     url: "http://localhost:5000/comments",
  //     method: "GET",
  //     headers: { "Content-Type": "application/json" },
  //   });

  //   const arr: any = Array.from(res?.data).reverse();

  //   setChats(arr);
  // };

  const handleDate = (value: string | Date) => {
    let result = value;

    const currentDate = new Date(value);
    const date = currentDate.getDate();
    const month = currentDate.getMonth() + 1;
    const hour = currentDate.getHours();
    const minute = currentDate.getMinutes();
    result = `${date}/${month} ${hour}:${minute}`;

    return result;
  };

  // Hooks
  // useEffect(() => {
  //   getChats();
  // }, []);

  // useEffect(() => {
  //   setInterval(() => {
  //     getChats();
  //   }, 300000);
  // }, [chats]);

  return (
    <div id="Chat" className={css.wrapper}>
      <div className={css.form}>
        <h1>Tulis ucapan</h1>
        <div>
          <input
            type="text"
            name="name"
            onChange={handleChange}
            placeholder="Nama"
            value={data?.name}
          />
        </div>
        <div>
          <textarea
            onChange={handleChange}
            name="message"
            placeholder="Tulis pesan Anda di sini"
            rows={5}
            value={data?.message}
          ></textarea>
        </div>
        <button
          type="button"
          disabled={!data?.name || !data?.message}
          onClick={handleClick}
        >
          Submit
        </button>
        <h1>Terimakasih</h1>
      </div>
      <div className={css.list}>
        <ul>
          {chats.map((item: ChatInterface, index: number) => (
            <li key={index.toString()}>
              <div className={css.chat}>
                <div className={css.chat_header}>
                  <strong>{item?.name}</strong>
                  <span>{handleDate(item?.date)}</span>
                </div>
                <div className={css.chat_body}>{item?.message}</div>
              </div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default Chat;
