import { useState, useEffect } from "react";

import css from "./counter.module.scss";

const Counter = () => {
  // States
  const [date, setDate] = useState(new Date("2021/10/14").getTime());
  const [day, setDay] = useState(0);
  const [hour, setHour] = useState(0);
  const [minute, setMinute] = useState(0);
  const [second, setSecond] = useState(0);

  // Hooks
  useEffect(() => {
    if (!date) {
      setDate(new Date("2021/10/14").getTime());
    }
  }, [date]);

  useEffect(() => {
    setInterval(() => {
      const now = new Date().getTime();

      const distance = date - now;

      setDay(Math.floor(distance / (1000 * 60 * 60 * 24)));
      setHour(
        Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
      );
      setMinute(Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)));
      setSecond(Math.floor((distance % (1000 * 60)) / 1000));
    }, 1000);
  }, [date]);

  return (
    <div className={css.wrapper}>
      <div className={css.image}>
        <div className={css.counter}>
          <div className={css.counter_header}>
            <h1>Hitung Mundur</h1>
          </div>

          <div className={css.counter_content}>
            <div className={css.counter_item}>
              <div>
                <h1>{day}</h1>
              </div>
              <p>Hari</p>
            </div>
            <div className={css.counter_item}>
              <div>
                <h1>{hour}</h1>
              </div>
              <p>Jam</p>
            </div>
            <div className={css.counter_item}>
              <div>
                <h1>{minute}</h1>
              </div>
              <p>Menit</p>
            </div>
            <div className={css.counter_item}>
              <div>
                <h1>{second}</h1>
              </div>
              <p>Detik</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Counter;
