import { useState } from "react";

import css from "./gallery.module.scss";

// Images
import Image1 from "../../static/images/gallery/1.jpg";
import Image2 from "../../static/images/gallery/2.jpg";
import Image3 from "../../static/images/gallery/3.jpg";
import Image4 from "../../static/images/gallery/4.jpg";
import Image5 from "../../static/images/gallery/5.jpg";
import Image6 from "../../static/images/gallery/6.jpg";
import Image7 from "../../static/images/gallery/7.jpg";
import Image8 from "../../static/images/gallery/8.jpg";
import Image9 from "../../static/images/gallery/9.jpg";
import Image10 from "../../static/images/gallery/10.jpg";
import Image11 from "../../static/images/gallery/11.jpg";
import Image12 from "../../static/images/gallery/12.jpg";
import Image13 from "../../static/images/gallery/13.jpg";
import Image14 from "../../static/images/gallery/14.jpg";
import Image15 from "../../static/images/gallery/15.jpg";
import Image16 from "../../static/images/gallery/16.jpg";
import Image17 from "../../static/images/gallery/17.jpg";

const Gallery = () => {
  // States
  const [showImage, setShowImage] = useState({
    name: "Image1",
    file: Image1,
  });

  // Variables
  const images = [
    { name: "Image1", file: Image1 },
    { name: "Image2", file: Image2 },
    { name: "Image3", file: Image3 },
    { name: "Image4", file: Image4 },
    { name: "Image5", file: Image5 },
    { name: "Image6", file: Image6 },
    { name: "Image7", file: Image7 },
    { name: "Image8", file: Image8 },
    { name: "Image9", file: Image9 },
    { name: "Image10", file: Image10 },
    { name: "Image11", file: Image11 },
    { name: "Image12", file: Image12 },
    { name: "Image13", file: Image13 },
    { name: "Image14", file: Image14 },
    { name: "Image15", file: Image15 },
    { name: "Image16", file: Image16 },
    { name: "Image17", file: Image17 },
  ];

  return (
    <div id="Gallery" className={css.wrapper}>
      <h1>Our Moments</h1>
      <div className={css.image}>
        <img alt={showImage?.name} src={showImage?.file} />
      </div>
      <div className={css.images}>
        {images.map((item, i) => (
          // eslint-disable-next-line jsx-a11y/img-redundant-alt
          <img
            alt={`Image ${i}`}
            src={item?.file}
            className={showImage?.name === item?.name ? css.active : ""}
            onClick={() => setShowImage(item)}
          />
        ))}
      </div>
    </div>
  );
};

export default Gallery;
