import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";

import css from "./navbar.module.scss";

const Navbar = () => {
  // States
  const [active, setActive] = useState("");

  // Variables
  const { hash } = useLocation();

  // Hooks
  useEffect(() => {
    if (hash) {
      setActive(hash.replace("#", ""));
    }
  }, [hash]);

  // Handlers
  const isActive = (name: string) => {
    if (active === name) return css.active;

    return "";
  };

  return (
    <nav className={css.nd_navbar}>
      <ul className={css.nd_navbar_list}>
        <li className={css.nd_navbar_item}>
          <a href="#Home" className={isActive("Home")}>
            <i className="material-icons">cabin</i>
          </a>
        </li>
        <li className={css.nd_navbar_item}>
          <a href="#Profile" className={isActive("Profile")}>
            <i className="material-icons">favorite_border</i>
          </a>
        </li>
        <li className={css.nd_navbar_item}>
          <a href="#Schedule" className={isActive("Schedule")}>
            <i className="material-icons">event</i>
          </a>
        </li>
        <li className={css.nd_navbar_item}>
          <a href="#Gallery" className={isActive("Gallery")}>
            <i className="material-icons">auto_awesome_mosaic</i>
          </a>
        </li>
        <li className={css.nd_navbar_item}>
          <a href="#Chat" className={isActive("Chat")}>
            <i className="material-icons">chat</i>
          </a>
        </li>
      </ul>
    </nav>
  );
};

export default Navbar;
