import { useState } from "react";

import css from "./splash.module.scss";

// Components
import NukuDita from "components/NukuDita";

const Splash = () => {
  // States
  const [show, setShow] = useState(true);

  // Handlers
  const handleClick = () => {
    setShow(!show);
    const audioElement: any = document.querySelector("#Music");
    audioElement.play();
  };

  return (
    <div className={css.wrapper} style={{ display: show ? "fixed" : "none" }}>
      <div className={css.overlay}>
        <div className={css.content}>
          <h3>We're happy to invite you</h3>
          <h3>to our wedding</h3>
          <NukuDita styles={{ margin: 20 }} />
          <button type="button" onClick={handleClick}>
            OPEN INVITATION
          </button>
        </div>
      </div>
    </div>
  );
};

export default Splash;
